/* Imports ----------------------------------------------------------------- */

// D3 imports commented out for development: UNCOMMENT for production
import "whatwg-fetch";
import * as d3 from "d3";

// Global constants ----------------------------------------------------------

const FILE_CONSTITUENCIES = "constituencies.csv";
const FILE_LABELS = "labels.csv";
const MAPNAMES = ["party", "gender", "experience", "gains", "losses"];
const HIGHLIGHT_COLOR = "#000000";
const MIN_X = 8;
const MAX_X = 55;
const MIN_Y = 4;
const MAX_Y = 78;
const MID_X = MIN_X + Math.ceil((MAX_X - MIN_X) / 2)

/* Functions: Set up visualisation ----------------------------------------- */

function setup(constituencies, labels, mapname) {

    // Get the summary data
    const summaries = getSummaryData(constituencies);

    // Setup the tabs
    const nav = d3.select("#nav");

    nav.selectAll(".tab")
        .data(MAPNAMES)
        .enter()
        .append("span")
        .attr("class", d => {
            if (d == MAPNAMES[MAPNAMES.length - 1]) {
                return "tab tabend"
            } else {
                return "tab"
            }
        })
        .attr("id", d => d)
        .html(d => d.charAt(0).toUpperCase() + d.slice(1))
        .on("mouseover", function(d) {
            d3.select(this).classed("tabhover", true);
        })
        .on("mouseout", function(d) {
            d3.select(this).classed("tabhover", false);
        })
        .on("click", d => update(d, summaries));

    // Set the active tab
    d3.select("#" + mapname).classed("tabactive", true);

    // Select infobox element
    const infobox = d3.select("#infobox");

    // Sizes and margins for the svg
    const margin = {top: 25, right: 25, bottom: 25, left: 25};
    const width = 850 - margin.left - margin.right;
    const height = 1300 - margin.top - margin.bottom;

    // Create the canvas svg element
    const vis = d3.select("#vis")
        .append("svg")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform", `translate(${margin.left},${margin.top})`);

    // Create the legend group
    const legend = vis.append("g")
        .attr("id", "legend")
        .attr("transform", `translate(700, 0)`);

    // Add background canvas for click events
    vis.append("rect")
        .attr("width", "100%")
        .attr("height", "100%")
        .attr("stroke", "#ffffff")
        .attr("stroke-width", 0)
        .attr("stroke-opacity", 0)
        .attr("fill", "#ffffff")
        .attr("fill-opacity", 0)
        .on("click", getMouseOutHandler(mapname, infobox));

    // Create the xscale
    const xscale = d3.scaleLinear()
        .domain([MIN_X, MAX_X])
        .range([0, width]);

    // Create the yscale
    const yscale = d3.scaleLinear()
        .domain([MIN_Y, MAX_Y])
        .range([height, 0]);

    // Append the squares
    vis.selectAll(".square")
        .data(constituencies)
        .enter()
        .append("rect")
        .attr("class", "square")
        .attr("x", d => {
            return xscale(d.xindex);
        })
        .attr("y", d => {
            return yscale(d.yindex);
        })
        .attr("width", (width / (MAX_X - MIN_X)) - 2)
        .attr("height", (height / (MAX_Y - MIN_Y)) - 2)
        .attr("fill", getFillHandler(mapname))
        .on("mouseover", getMouseOverHandler(mapname, infobox))
		.on("mouseout", getMouseOutHandler(mapname, infobox));

    // Append the labels
    vis.selectAll(".label")
        .data(labels)
        .enter()
        .append("text")
        .attr("class", "grouplabel")
        .attr("x", d => xscale(d.xpos))
        .attr("y", d => yscale(d.ypos))
        .style("text-anchor", d => d.textanchor)
        .append("tspan")
        .attr("x", d => xscale(d.xpos))
        .attr("dy", 0)
        .text(d => d.grouplabela)
        .append("tspan")
        .attr("x", d => xscale(d.xpos))
        .attr("dy", 14)
        .text(d => d.grouplabelb);

    drawLegend(mapname, summaries);
}

/* Functions: Update visualisation ----------------------------------------- */

function update(mapname, summaries) {

    // Update the URL
    const url = `?map=${mapname}`;
    history.replaceState(null, null, url);

    // Update the active tab
    d3.selectAll(".tabactive")
        .attr("class", d => {
            if (d == MAPNAMES[MAPNAMES.length - 1]) {
                return "tab tabend"
            } else {
                return "tab"
            }
        });

    d3.select("#" + mapname).classed("tabactive", true);

    // Select infobox element
    const infobox = d3.select("#infobox");

    // Update the squares
    d3.selectAll(".square")
        .attr("fill", getFillHandler(mapname))
        .on("mouseover", getMouseOverHandler(mapname, infobox))
		.on("mouseout", getMouseOutHandler(mapname, infobox));

    // Update the legend
    clearLegend();
    drawLegend(mapname, summaries);
}

/* Functions: Event handlers factories ------------------------------------- */

function getFillHandler(mapname) {
    return d => {
        switch (mapname) {

            case "gender":

                return getGenderColor(d.gender);
                break;

            case "experience":

                return getExperienceColor(d.experience);
                break;

            case "gains":

                return getPartyColor(d.gain_party);
                break;

            case "losses":

                return getPartyColor(d.loss_party);
                break;

            default:

                return getPartyColor(d.party);
                break;
        }
    };
}

function getMouseOverHandler(mapname, infobox) {
    return (d, i, n) => {

        d3.select(n[i])
            .attr("stroke", HIGHLIGHT_COLOR)
            .attr("stroke-width", 2.0)
            .attr("stroke-opacity", 1.0)

        let constituency = d.constituency,
            xindex = d.xindex,
            name = d.mp,
            party = null,
            outcome = null,
            color = null;

        switch (mapname) {

            case "gender":

                outcome = d.gender;
                color = getGenderColor(d.gender);
                party = d.party === "Undeclared" ? "" : d.party;
                break;

            case "experience":

                outcome = d.experience;
                color = getExperienceColor(d.experience);
                party = d.party === "Undeclared" ? "" : d.party;
                break;

            case "gains":

                outcome = d.gain_result;
                color = getPartyColor(d.gain_party);
                name = "";
                break;

            case "losses":

                outcome = d.loss_result;
                color = getPartyColor(d.loss_party);
                name = "";
                break;

            default:

                outcome = d.result;
                color = getPartyColor(d.party);
                break;
        }

        // Show infobox
        showInfoBox(
            infobox,
            constituency,
            name,
            party,
            outcome,
            color,
            xindex);
    };
}

function getMouseOutHandler(mapname, infobox) {
    return (d, i, n) => {

        d3.select(n[i])
            .attr("stroke", "#ffffff")
            .attr("stroke-width", 0)
            .attr("stroke-opacity", 0)

        // Hide infobox
        hideInfoBox(infobox);
    };
}

/* Functions: Colors ------------------------------------------------------- */

// Get the color from an object of colors with a key or return a default color
function getColor(colors, key) {

	let c = "#d8d8d8";
	if (colors.hasOwnProperty(key)) c = colors[key];
	return c;
}

// Get the color for a party
function getPartyColor(party) {

	const colors = {
		"Con": "#0063ba",
		"Lab": "#d50000",
		"LD": "#faa01a",
		"Brexit": "#12b6cf",
		"Green": "#78b82a",
		"SNP": "#f7db15",
		"PC": "#348837",
		"DUP": "#d46a4c",
		"SF": "#02665f",
		"UUP": "#a1cdf0",
		"SDLP": "#4ea268",
		"Alliance": "#cdaf2d",
        "Oth": "#909090"
	}

	return getColor(colors, party);
}

// Get the color for the gender
function getGenderColor(gender) {

	const colors = {
		"Male": "#00a6d6",
		"Female": "#ffcc00",
		"Non-binary": "#20c000"
	}

	return getColor(colors, gender);
}

// Get the color for the experience
function getExperienceColor(experience) {

	const colors = {
		"Sitting MP": "#6acaf7",
		"Returning MP": "#016796",
		"New MP": "#ff6296"
	}

	return getColor(colors, experience);
}

/* Functions: Infobox ------------------------------------------------------ */

// Shows the infobox with the given data
function showInfoBox(
    infobox,
    constituency,
    name,
    party,
    outcome,
    color,
    xindex) {

	const partyLabel = (party) ? " (" + party + ")" : "";

	const html = "<p class=\"constituency\">" + constituency + "</p>" +
		"<p>" + name + partyLabel + "</p>" +
		"<p class=\"outcome\" style=\"color: " + color + ";\">" +
		outcome + "</p>";

    // Set infobox content
    infobox.html(html);

    // Draw infobox
    const box = infobox.node().getBoundingClientRect();
    const boxHeight = box.height;
    const boxWidth = box.width;

    let infoBoxPosX = d3.event.pageX,
        infoBoxPosY = d3.event.pageY - boxHeight;

    if (xindex > MID_X) infoBoxPosX = d3.event.pageX - boxWidth;

    infobox
        .style("left", infoBoxPosX + "px")
        .style("top", infoBoxPosY + "px");

	infobox.style("opacity", 1.0);
}

// Hides the infobox
function hideInfoBox(infobox) {

	infobox.style("opacity", 0);
}

/* Functions: Legend ------------------------------------------------------- */

function getSummaryData(constituencies) {

    const parties = {
        "Con": {party: "Con", label: "Conservative", number: 0},
        "Lab": {party: "Lab", label: "Labour", number: 0},
        "SNP": {party: "SNP", label: "SNP", number: 0},
        "LD": {party: "LD", label: "Liberal Democrat", number: 0},
        "PC": {party: "PC", label: "Plaid Cymru", number: 0},
        "Green": {party: "Green", label: "Green", number: 0},
        "DUP": {party: "DUP", label: "DUP", number: 0},
        "SF": {party: "SF", label: "Sinn Fein", number: 0},
        "SDLP": {party: "SDLP", label: "SDLP", number: 0},
        "Alliance": {party: "Alliance", label: "Alliance", number: 0},
        "Oth": {party: "Oth", label: "Other", number: 0}
    };

    const genders = {
        "Male": {gender: "Male", label: "Male MP", number: 0},
        "Female": {gender: "Female", label: "Female MP", number: 0},
        "Non-binary": {gender: "Non-binary", label: "Non-binary MP", number: 0}
    };

    const experiences = {
        "Sitting MP": {experience: "Sitting MP", label: "Sitting MP", number: 0},
        "Returning MP": {experience: "Returning MP", label: "Returning MP", number: 0},
        "New MP": {experience: "New MP", label: "New MP", number: 0}
    };

    const gains = {
        "Con": {gain_party: "Con", label: "Conservative", number: 0},
        "Lab": {gain_party: "Lab", label: "Labour", number: 0},
        "SNP": {gain_party: "SNP", label: "SNP", number: 0},
        "LD": {gain_party: "LD", label: "Liberal Democrat", number: 0},
        "PC": {gain_party: "PC", label: "Plaid Cymru", number: 0},
        "Green": {gain_party: "Green", label: "Green", number: 0},
        "DUP": {gain_party: "DUP", label: "DUP", number: 0},
        "SF": {gain_party: "SF", label: "Sinn Fein", number: 0},
        "SDLP": {gain_party: "SDLP", label: "SDLP", number: 0},
        "Alliance": {gain_party: "Alliance", label: "Alliance", number: 0},
        "Oth": {gain_party: "Oth", label: "Other", number: 0},
    };

    const losses = {
        "Con": {loss_party: "Con", label: "Conservative", number: 0},
        "Lab": {loss_party: "Lab", label: "Labour", number: 0},
        "SNP": {loss_party: "SNP", label: "SNP", number: 0},
        "LD": {loss_party: "LD", label: "Liberal Democrat", number: 0},
        "PC": {loss_party: "PC", label: "Plaid Cymru", number: 0},
        "Green": {loss_party: "Green", label: "Green", number: 0},
        "DUP": {loss_party: "DUP", label: "DUP", number: 0},
        "SF": {loss_party: "SF", label: "Sinn Fein", number: 0},
        "SDLP": {loss_party: "SDLP", label: "SDLP", number: 0},
        "Alliance": {loss_party: "Alliance", label: "Alliance", number: 0},
        "Oth": {loss_party: "Oth", label: "Other", number: 0},
    };

    constituencies.forEach(d => {

        parties[d.party].number = parties[d.party].number + 1;
        genders[d.gender].number = genders[d.gender].number + 1;
        experiences[d.experience].number = experiences[d.experience].number + 1;

        if (d.gain_party !== "") {
            if (gains.hasOwnProperty(d.gain_party)) {
                gains[d.gain_party].number = gains[d.gain_party].number + 1;
            } else {
                gains["Oth"].number = gains["Oth"].number + 1;
            }
        }

        if (d.loss_party !== "") {
            if (losses.hasOwnProperty(d.loss_party)) {
                losses[d.loss_party].number = losses[d.loss_party].number + 1;
            } else {
                losses["Oth"].number = losses["Oth"].number + 1;
            }
        }
    });

    const partySummaries = [];
    Object.keys(parties).forEach(function(key) {
        partySummaries.push(parties[key]);
    });

    const genderSummaries = [];
    Object.keys(genders).forEach(function(key) {
        genderSummaries.push(genders[key]);
    });

    const experienceSummaries = [];
    Object.keys(experiences).forEach(function(key) {
        experienceSummaries.push(experiences[key]);
    });

    const gainSummaries = [];
    Object.keys(gains).forEach(function(key) {
        gainSummaries.push(gains[key]);
    });

    const lossSummaries = [];
    Object.keys(losses).forEach(function(key) {
        lossSummaries.push(losses[key]);
    });

    const summaries = {
        party: partySummaries,
        gender: genderSummaries,
        experience: experienceSummaries,
        gains: gainSummaries,
        losses: lossSummaries
    };

    return summaries;
}

function drawLegend(mapname, summaries) {

    const width = (800 / (MAX_X - MIN_X)) - 2;
    const height = (1250 / (MAX_Y - MIN_Y)) - 2;
    const padding = 14;

    const legend = d3.select("#legend");

    legend.selectAll(".legendsquare")
        .data(summaries[mapname])
        .enter()
        .append("rect")
        .attr("class", "legendsquare")
        .attr("x", 100)
        .attr("y", (d, i) => i * (height + padding))
        .attr("width", width)
        .attr("height", height)
        .attr("fill", getFillHandler(mapname));

    legend.selectAll(".legendlabel")
        .data(summaries[mapname])
        .enter()
        .append("text")
        .attr("class", "legendlabel")
        .style("text-anchor", "end")
        .attr("x", 90)
        .attr("y", (d, i) => (i * (height + padding)) + height - 1)
        .text(d => `${d.label} (${d.number})`);
}

function clearLegend() {

    const legend = d3.select("#legend");
    legend.selectAll("*").remove();
}

/* Functions: Utilities ---------------------------------------------------- */

// Gets the parameter of the given name from the query string
function getQueryParameter(variable) {

	const query = window.location.search.substring(1);
	const vars = query.split("&");

	for (let i = 0; i < vars.length; i++) {

		let pair = vars[i].split("=");
		if (pair[0] == variable) {return pair[1];}
	}

	return(false);
}

/* Main -------------------------------------------------------------------- */
Promise.all([
    d3.csv(FILE_CONSTITUENCIES),
    d3.csv(FILE_LABELS)])
.then(([constituencies, labels]) => {

    // Get the mapname parameter and assign it, the default is "party"
    const mapquery = getQueryParameter("map") || "party";
    const mapname = (MAPNAMES.includes(mapquery)) ? mapquery : "party";

    // Set up the visualisation
    setup(constituencies, labels, mapname);
});
